html, body {
  height: 100%;
}

.page__wrapper {
  min-height: 100%;
}

* html .page__wrapper {
  height: 100%;
}

body {
  text-align: center;
  font-family: 'Exo', sans-serif;
  color: #282828;
}

h2 {
  font-size: 40px;
}

h3 {
  color: #ff6d6d;
  font-size: 16px;
}

h4 {
  font-size: 22px;
}

p {
  font-size: 16px;
  font-weight: 500;
}

.container {
  max-width: 1070px;
  margin: auto;
}

.flex {
  display: flex;
}

.flex div {
  margin: 0 15px;
}

.flex img {
  height: 75px;
  width: 75px;
}

.features-1 {
  background-image: url("../../static/img/content/BG.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 220px 0;
}

.testimonials {
  background-image: url("../../static/img/content/t-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 110px 0 75px;
}

.testimonials p {
  color: #fff;
  opacity: 0.7;
  font-weight: 400;
}

.testimonials h4 {
  color: #fff;
}

.testimonials .logos {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 100px;
}

.features {
  background: #f6f5f3;
  padding: 100px 0 0;
}

.features h4 {
  color: #fff;
}

.features .container {
  max-width: 810px;
}
